                const bIds = bId.split(':');
                return {
                    walletAddress: bIds[2],
                    chainId: bIds[1],
                    chainData: this.chainIdChainMap[bIds[1]]
                }
            }
        },

        async invokeMetamask(chainId) {
        try{
            this.isLoading = true
            this.web3 = await loadweb3(chainId)
            const accounts = await this.web3.eth.getAccounts();
            if (!this.isEditing) {
                this.walletAddress = {
                    address: "",
                    chainId: ""
                }
                this.walletAddress.address = accounts[0]
                this.walletAddress.chainId = parseInt(`${chainId}`)
            } else {
                this.walletAddressEdit = {
                    address: "",
                    chainId: ""
                }
                this.walletAddressEdit.address = accounts[0]
                this.walletAddressEdit.chainId = parseInt(`${chainId}`)
            }
            this.closeWalletPopup()
            this.isLoading = false
            await this.sign()
        }catch(e){          
          this.isLoading = false          
          this.notifyErr(e) 
        }      
        },

        async invokeKeplr(chainId) {
            this.isLoading = true
            const uintarrayaddress = await window.keplr.getKey(chainId)
            const base58 = baseX('123456789ABCDEFGHJKLMNPQRSTUVWXYZabcdefghijkmnopqrstuvwxyz')
            const publicKey = 'z' + base58.encode(uintarrayaddress.pubKey)
            if (!this.isEditing) {

                this.walletAddress.address = uintarrayaddress.bech32Address
                this.walletAddress.chainId = chainId
                this.walletAddress.uint8Address = publicKey


            } else {

                this.walletAddressEdit.address = uintarrayaddress.bech32Address
                this.walletAddressEdit.chainId = chainId
                this.walletAddressEdit.uint8Address = publicKey

            }
            this.isLoading = false
            this.closeWalletPopup()

            await this.sign()
        },




        addWallet() {
            this.isEditing = true;
            this.openWalletPopup('add-wallet');
        },

        async verify(message, walletAddress, signature) {
            let recoveredWalletAddress = await this.web3.eth.personal.ecRecover(message, signature);
            recoveredWalletAddress = Web3.utils.toChecksumAddress(recoveredWalletAddress)
            if (recoveredWalletAddress !== walletAddress) {
                return false
            }
            return true
        },
        mmSelected() {
            try {
                if (ethereum && ethereum.isMetaMask) {
                    console.log('mm detected')
                }
            } catch (error) {
                this.noMetamask = true;
                return this.notifyErr('Install Metamask Extension')
            }
            this.isMetamaskSelected = true
            this.isKeplrSelected = false
        },
        keplrSelected() {
            if (!window.keplr) {
                return this.notifyErr('Install Keplr Extension')
            }
            this.isMetamaskSelected = false
            this.isKeplrSelected = true
        },
        toggleWallet() {
            this.isMetamaskSelected = false
            this.isKeplrSelected = false
        },
        async sign() {
            try {
                this.isLoading = true
                if (!this.isEditing) {
                    if (!this.walletAddress) {
                        throw new Error('Wallet address is not set')
                    }

                    let didId = `did:hid:testnet:${this.walletAddress.address}`

                    // Step 0:  Resolve a DID and see if its already
                    const data = await this.resolveADID({ didId })
                    if (data) {
                        this.setDIDDocument(data)
                        this.setDID(didId)
                        this.isLoading = false;
                        // //// TODO: open this later

                        const encData = await this.$store.dispatch('fetchCredentials')
                        if (encData.length > 0) {
                            this.edvExists = true
                            encData.forEach((item) => {
                                this.$store.commit('addCredentialsToStore', { encrypted: true, credential: item })
                            })
                            this.overlayEffect = new Array(encData.length).fill(false)



                        }
                        this.loadAllBalances()
                        return;
                    } else {
                        console.error(data)
                    }
                    // Step 1:  Create a DID 
                    const payload = {
                        walletAddress: this.walletAddress.address,
                        chainId: typeof (this.walletAddress.chainId) === 'number' ? this.web3.utils.toHex(this.walletAddress.chainId) : this.walletAddress.chainId
                    }
                    payload.keplr = typeof (this.walletAddress.chainId) === 'number' ? false : true

                    payload.publicKey = this.walletAddress.uint8Address
                    await this.createADID(payload)

                    // Step 1:  Sign the DID using eth personal sign
                    let sig;
                    if (payload.keplr) {
                        const didDocBytes = (await Did.encode(this.getUnChangedDIDDOcJSON)).finish();
                        sig = await window.keplr.requestMethod("signArbitrary", [
                            this.walletAddress.chainId,
                            this.walletAddress.address,
                            didDocBytes,
                        ]);

                        const vmId = this.didDocument.verificationMethod.find(x => x.id.includes(this.walletAddress.address) ? x.id : undefined)
                        await this.registerADID({
                            verificationMethodId: vmId.id,
                            signature: sig,
                            address: this.walletAddress.address
                        })
                    } else {
                        sig = await this.web3.eth.personal.sign(this.getDIDDocJSONString, this.walletAddress.address)
                        const vmId = this.didDocument.verificationMethod.find(x => x.id.includes(this.walletAddress.address) ? x.id : undefined)
                        await this.registerADID({
                            verificationMethodId: vmId.id,
                            signature: sig,

                        })
                    }
                    // Step 3:  Register the DID on hypersign network



                    this.loadAllBalances()
                } else {
                    if (!this.walletAddressEdit) {
                        throw new Error('Wallet address is not set')
                    }

                    if (!this.didDocument) {
                        return this.notifyErr('DID doc is not generated')
                    }
                    let keplr = typeof (this.walletAddressEdit.chainId) === 'number' ? false : true

                    const blockchainId = `${keplr ? "cosmos" : "eip155"}:${this.walletAddressEdit.chainId}:${this.walletAddressEdit.address}`;
                    if (keplr) {
                        const publicKey = this.walletAddressEdit.uint8Address
                        await this.addVerificationMethod({ blockchainId, publicKey })
                    } else {
                        await this.addVerificationMethod({ blockchainId })

                    }

                    // const signerVm = this.didDocument.verificationMethod.find(x => x.blockchainAccountId == bcAccountId);
                    // if (!signerVm) {
                    //     console.error("No verification method found")
                    //     return;
                    // }
                    // const sig = await this.web3.eth.personal.sign(
                    //     this.getDIDDocJSONString, this.walletAddressEdit.address
                    // )           
                }
                this.isLoading = false
            } catch (e) {
                this.clear()
                this.notifyErr(e.message)
            } finally {
                this.isLoading = false
                this.didId = this.did
            }
        },
        async signWithMetamask(chainID, wallet) {
            try{
            const id = this.web3.utils.toHex(chainID)
            // await ethereum.request({
            //             method: 'wallet_requestPermissions',
            //             params: [{ eth_accounts: { wallet } }],
            //         })

            await ethereum.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: id }]
            })
            await ethereum.request({
                method: 'wallet_requestPermissions',
                params: [{
                    eth_accounts: {
                        requiredMethods: ['personal_sign']
                    }
                }],
            }).then((permissions) => {
                const accountsPermission = permissions.find(
                    (permission) => permission.parentCapability === 'eth_accounts'
                );
                if (accountsPermission) {
                    console.log('eth_accounts permission successfully requested!');
                }
            })
                .catch((error) => {
                    if (error.code === 4001) {
                        // EIP-1193 userRejectedRequest error
                        console.log('Permissions needed to continue.');
                    } else {
                        console.error(error);
                    }
                });

            const message = this.getDIDDocJSONString
            const sig = await window.ethereum.request({
                method: 'personal_sign',
                params: [message, wallet,],
            })
            return sig
            }catch(e){                
                throw new Error(e.message)
            }

        }, sleep(ms) {
            return new Promise(resolve => setTimeout(resolve, ms));
        },
        async signWithKeplr(chainID, address) {
            await this.sleep(1000)
            const didDocBytes = (await Did.encode(this.getUnChangedDIDDOcJSON)).finish();
            const sig = await window.keplr.requestMethod("signArbitrary", [
                chainID,
                address,
                didDocBytes,
            ]);
            return sig

        },
        async saveChanges() {
            try {
                this.isLoading = true
                const signInfos = []
                let sig;
                if (!this.did) {
                    throw new Error('No DID to update')
                }
                const didDocResolved = await this.resolveADID({ didId: this.did })

                if (didDocResolved) {
                    // if exists
                    const allWallets = didDocResolved.verificationMethod.filter(x => {
                        if (x.id.includes('key-1')) {
                            return x
                        }
                    });
                    const t = allWallets[0].blockchainAccountId
                    if (t.split(':')[0].includes('eip')) {
                        const chainID = this.web3.utils.toHex(t.split(':')[1])
                        const address = t.split(':')[2]
                        await ethereum.request({
                            method: 'wallet_switchEthereumChain',
                            params: [{ chainId: chainID, }]
                        })
                        await ethereum.request({
                            method: 'wallet_requestPermissions',
                            params: [{
                                eth_accounts: {}
                            }],
                        }).then((permissions) => {
                            const accountsPermission = permissions.find(
                                (permission) => permission.parentCapability === 'eth_accounts'
                            );
                            if (accountsPermission) {
                                console.log('eth_accounts permission successfully requested!');
                            }
                        })
                            .catch((error) => {
                                if (error.code === 4001) {
                                    // EIP-1193 userRejectedRequest error
                                    throw new Error(error.message)
                                } else {
                                    console.error(error);
                                }
                            });



                        if (!t) {
                            this.isEditing = false;
                            this.isLoading = false

                            throw new Error(`Connect either of these accounts to save ` + allWallets.toString())
                        }


                        // Get the signature 
                        const message = this.getDIDDocJSONString
                        // const sig = await this.web3.eth.personal.sign(
                        //     message,this.web3.utils.toChecksumAddress(address) 
                        // )
                        sig = await window.ethereum.request({
                            method: 'personal_sign',
                            params: [message, address],
                        })
                        signInfos.push({
                            verification_method_id: `did:hid:testnet:${this.walletAddress.address}#key-1`,
                            signature: sig,
                            clientSpec: {
                                type: 'eth-personalSign',
                                adr036SignerAddress: ''
                            }
                        })
                    } else {
                        const address = t.split(':')[2]
                        const chainID = t.split(':')[1]
                        const didDocBytes = (await Did.encode(this.getUnChangedDIDDOcJSON)).finish();
                        sig = await window.keplr.requestMethod("signArbitrary", [
                            chainID,
                            address,
                            didDocBytes,
                        ]);
                        signInfos.push({
                            verification_method_id: `did:hid:testnet:${this.walletAddress.address}#key-1`,
                            signature: sig.signature,
                            clientSpec: {
                                type: 'cosmos-ADR036',
                                adr036SignerAddress: address
                            }
                        })
                    }
                    // verify the signateur

                    // Update the dID




                    const allvms = this.getUnChangedDIDDOcJSON.verificationMethod
                    const registerdVms = didDocResolved.verificationMethod
                    const newvms = allvms.filter(x => {
                        return false === registerdVms.some(y => x.blockchainAccountId === y.blockchainAccountId)
                    })
                    for (let id in newvms) {
                        const blockchainAccountId = newvms[id].blockchainAccountId
                        const chainID = blockchainAccountId.split(':')[1]
                        const walletAddr = blockchainAccountId.split(':')[2]
                        if (blockchainAccountId.includes('cosmos')) {
                            const sig = await this.signWithKeplr(chainID, walletAddr)
                            signInfos.push({
                                verification_method_id: newvms[id].id,
                                signature: sig.signature,
                                clientSpec: {
                                    type: 'cosmos-ADR036',
                                    adr036SignerAddress: walletAddr
                                }
                            })
                        } else {
                            const sig = await this.signWithMetamask(chainID, walletAddr)
                            signInfos.push({
                                verification_method_id: newvms[id].id,
                                signature: sig,
                                clientSpec: {
                                    type: 'eth-personalSign',
                                    adr036SignerAddress: ''
                                }
                            })

                        }
                    }
                    this.updateDID(signInfos);

                    return;
                } else {
                    // else then generate a new
                    console.log('Could not resolve the DID for id = ' + this.did)

                }

                this.isLoading = false
            } catch (e) {
                this.isLoading = false
                this.notifyErr(e.message)

            }

        },

        async updateDID(signInfos) {

            try {

                if (!this.did) {
                    throw new Error('No DID to update')
                }

                // if (sig && walletAddress) {
                //     const verified = await this.verify(message, walletAddress, sig)

                //     if (!verified) {
                //         throw new Error('Invaild signature')
                //     }
                // }

                //TODO: update (start from hhere)
                const payload = signInfos
                this.isLoading = true
                this.isEditing = false
                const data = await this.updateADID(payload)

                if (data.statusCode === 400) {
                    throw new Error(data.message)
                }
                this.isLoading = false
                this.notifySuccess('Successfully updated')
                this.clear()
            } catch (e) {
                this.isLoading = false

                this.notifyErr(e.message)
            }
        },

        async resolve() {
            try {
                this.isLoading = true
                this.cleanStates()
                this.clear();
                if (!this.didId.startsWith('did:hid:')) {
                    throw new Error('Enter a valid did')
                }
                const data = await this.resolveADID({ didId: this.didId })
                if (data) {
                    this.setDIDDocument(data)
                    this.setDID(this.didId)
                    this.loadAllBalances()
                } else {
                    throw new Error('Could not resolve the DID ' + this.did)
                }
            } catch (e) {
                this.notifyErr(e.message);
            } finally {
                this.isLoading = false
            }
        },

        clear() {
            this.isEditing = false
            this.showSaveButton = false
        },
        popLock(id) {

            document.getElementById(`lock-${id}`).classList.add('overlay-icon-pop')

        },
        popOutLock(id) {
            document.getElementById(`lock-${id}`).classList.remove('overlay-icon-pop')

        },
    },

    mixins: [notificationMixins, Utils]

}
</script>



