<template>
<div>
  <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage"></loading>   
     <b-sidebar backdrop width="50%" id="social-slider" title="Social Authentication"
      class="sidebarContainer background-transparent" right shadow no-close-on-backdrop backdrop-variant="dark">     
      <div class="px-3 py-2">
        <b-card>
          <b-card-title>{{stickyNoteTitle}}</b-card-title>
          <b-card-text>
             Create your decentralized credentials.
            DODO does not store, share or sell any of your data. The credentials issued are totally in users control and stored in their wallet.
          </b-card-text>          
        </b-card>
        <div class="row scroll mt-2">
            <div class="col-lg-4" v-for="card in cards" :key="card.id">
              <div>
                <b-card tag="article"
                :header="(card.provider).toUpperCase()" class="text-center mt-4"              
                  >
                  <div class="text-center pb-2">                  
                    <p>Get your social verifiable credential by authorizing {{card.provider}}</p>
                  </div>
                  <div class="text-center mt-2">
                  <b-button variant="primary" class="wallet-btn" @click="handleSocialLogin(card)">Authorize <img :src="card.logo" width="20px;" height="20px;"/></b-button> 
                  </div>
                </b-card>
              </div>
            </div>
          </div>
        </div>
    </b-sidebar>  
  </div>
</template>

<script>
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
import webAuth from "../mixins/auth0"
import { mapActions, mapState} from 'vuex'
import twitterLogo from "../assets/twitter.svg";
import githubLogo from "../assets/github.svg";
import discordLogo from "../assets/discordsv.svg";
export default {
  name: "SocialSlide",
  components: {    
        Loading
  },
  data() {
    return {   
      authToken:localStorage.getItem('authToken'),
      twitterAccessToken:"",
      stickyNoteTitle: "💡 InfoBox",      
      isLoading:false,
      fullPage: true, 
      cards:[
      {id:1,
      provider:"twitter",
      logo:twitterLogo},
      {id:2,
      provider:"github",
      logo:githubLogo},
      {id:3,
      provider:"discord",
      logo:discordLogo},      
      ]   
    };
  },
  mounted() {    
    // this.$root.$on("resetMarketPlaceSlide", () => {
    //   this.resetAllValues();
    // });
  },
  watch:{
    credentials: {
// eslint-disable-next-line
          async handler(newValue) {                            
              this.isLoading = false              
            },
            deep: true
        },
  },
  computed:{
    ...mapState(["credentials"]),       
  },
  methods: {     
    ...mapActions(['issueCredential']),
    handleSocialLogin(data) {
      try {
        this.isLoading = true        
          webAuth.popup.authorize(
            {
              connection: data.provider,
              owp: true,
          
            },
            // (err, authRes) => {
            //   if (!err && authRes.accessToken) {
            //     console.log(authRes)
            //     console.log(authRes.accessToken)                
            //     localStorage.setItem("twitterAccessToken",this.twitterAccessToken);                                
            //   }
            // }
            (err, authRes) => {
  
              if (!err) {
                webAuth.client.userInfo(
                  authRes.accessToken,
                  async (err, user) => {
                    if (err) {
                      throw new Error(err)
                    }
                  const dataToSendForCred={
                    email:user.email?user.email:undefined,
                    name:user.name,
                    nickname:user.nickname,
                    picture:user.picture,
                    sub:user.sub,
                  }       
                  this.issueCredential(dataToSendForCred)
                  this.$root.$emit('bv::toggle::collapse', 'social-slider')
                    // const socialUserId = await this.getTwitterScreenName(discordId)
                    // console.log(socialUserId)
                    // console.log(discordUserName)                              
                  }
                );
              }
            }
          );       
      } catch (e) {
        console.log(e)
      }
    },
    async getTwitterScreenName(twitterId) {
      try {
        this.isLoading = true;
        if (twitterId) {
          let url = `http://localhost:9002/api/v1/twitter/user/${twitterId}`;
          console.log(url)
          let headers = {
            "Content-Type": "application/json",
            Authorization: `Bearer ${this.authToken}`,
          };

          const resp = await fetch(url,{
            method: "GET",            
            header: headers,
          });
          console.log(resp)
          const json = await resp.json()
          console.log(json)         
        } else {
          return null;
        }
      } catch (e) {
        console.log(e)
      } finally {
        this.isLoading = false;
      }
    },
  },
  
};
</script>
