const moralisHost = 'https://deep-index.moralis.io/api/v2/'
const moralisHeaders = {
                'accept': 'application/json',
                'X-API-Key': '7VaD6UFyu5cFAE3A7uJrIsUahL9RDrJmZtZgcBMP8HunQ0WmCdzX2WKH0MTydGuw'
}

export async function fetchTokenPrice(tokenAddress, chain) {
    const address = tokenAddress;
    const url = `${moralisHost}erc20/${address}/price?chain=${chain}`
    const resp = await fetch(url, {
        headers: moralisHeaders
    })
    return await resp.json();
}

export async function getNativeBalance({ address, chain }) {
    const url = `${moralisHost}${address}/balance?chain=${chain}`
    const resp = await fetch(url, {
        headers: moralisHeaders
    })
    return await resp.json();
}

export async function getTokenBalance({ walletAddress, chain, tokenAddresses }) {
    const url = `${moralisHost}${walletAddress}/erc20?chain=${chain}&token_addresses=${tokenAddresses}`
    const resp = await fetch(url, {
        headers: moralisHeaders
    })
    return await resp.json();
}

